<template>
    <div>
        <h1>Wanted List</h1>

        <ul v-for="part in getList()" :key="part">
            {{part}}
        </ul>
    </div>
</template>

<script>
    import MonsterDataService from '@/dataService/monster.data.service.js';

    export default {
        name: "WantedList",
        computed: {
            monsters() {
                return MonsterDataService.getMonsters();
            }
        },
        methods: {
            getList() {
                var monsterParts = MonsterDataService.getParts();
                var randomParts = [];

                for (var i = 0; i <= 50; i++) {
                    var index = [Math.floor(Math.random() * (monsterParts.length - 1))];
                    randomParts.push(monsterParts.splice(index, 1)[0]);

                }

                return randomParts;
            }
        }
    }
</script>

